.menu {
   display: flex;
   justify-content: space-between;
   align-items: center;
   background-color: var(--black-color);
   height: 60px;
}

.link {
   text-decoration: none;
}

.list {
   list-style: none;
}

.nav {
   display: flex;
   align-items: center;
   justify-content: space-between;
   padding: 0px 32px;
   flex-grow: 1;
}

.nav-list {
   display: flex;
   flex-flow: nowrap;
   padding: 8px 0;
   gap: 40px;
}

.cinemania {
   color: var(--white-color);
   font-family: "Roboto", sans-serif;
   font-size: 24px;
   font-weight: 400;
   line-height: 28.13px;
   text-align: center;
}

.logo {
   display: flex;
   align-items: center;
   gap: 8px;
   z-index: 5;
}

.navigation {
   color: var(--grey-color);
   font-size: 14px;
   font-family: "Roboto", sans-serif;
   font-weight: 400;
}
.navigation:hover,
.navigation:active {
   color: var(--orange-color);
}

.nav-mobile {
   transform: translate(-100%);
   transition-duration: 500ms;
   width: 204px;
   height: 100%;
   background-color: var(--black-color);
   border-radius: 0 8px 8px 0;
   position: absolute;
   top: 0;
}

.nav-list-mobile {
   display: flex;
   gap: 24px;
   flex-direction: column;
   margin-left: 20px;
   margin-top: 88px;
}

.text-mobile {
   color: var(--white-color);
   font-weight: 400;

}

.navigation-activ {
   color: var(--orange-color);
}

.button-menu {
   background-color: transparent;
   border: none;
   font-family: "Roboto", sans-serif;
   font-size: 12px;
   font-weight: 500;
   line-height: 14.06px;
   text-align: center;
   color: var(--grey-color);
   cursor: pointer;
   display: none;
   z-index: 5;
}

.backdrop {
   width: 100%;
   height: 100%;
   background-color: #FFFFFF33;
   position: fixed;
   top: 0;
   transform: translate(-100%);
   transition-duration: 500ms;
}

.hidden {
   transform: translate(0%);

}

/* BUTTON */

.toggle-button {
   width: 60px;
   height: 24px;
   border-radius: 194px;
   border: none;
   background: linear-gradient(141.22deg, #FFC226 9.4%, #F84119 91.91%);
   display: flex;
   gap: 24px;
   align-items: center;
   padding: 0px 4px;
   cursor: pointer;
}

.dark {
   display: none;
}

@media screen and (max-width: 767px) {

   .button-menu,
   .nav-mobile {
      display: flex;
   }

   .cinemania,
   .nav-list {
      display: none;
   }

   .mobile-menu {
      display: flex;
      justify-content: space-between;
      align-items: center;
   }

   .menu {
      height: 48px;
   }

   .logo {
      width: 46px;
   }
   .toggle-button {
      width: 44px;
      height: 18px;
      padding: 0px 3px;
      gap: 14px;
   }
}

body[data-theme=dark] .menu {
   background-color: var(--black-color);
}

body[data-theme=light] .menu {
   background-color: var(--white-color);
}