.menu {
  background-color: var(--black-color);
  height: 60px;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.link {
  text-decoration: none;
}

.list {
  list-style: none;
}

.nav {
  flex-grow: 1;
  justify-content: space-between;
  align-items: center;
  padding: 0 32px;
  display: flex;
}

.nav-list {
  flex-flow: row;
  gap: 40px;
  padding: 8px 0;
  display: flex;
}

.cinemania {
  color: var(--white-color);
  text-align: center;
  font-family: Roboto, sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 28.13px;
}

.logo {
  z-index: 5;
  align-items: center;
  gap: 8px;
  display: flex;
}

.navigation {
  color: var(--grey-color);
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 400;
}

.navigation:hover, .navigation:active {
  color: var(--orange-color);
}

.nav-mobile {
  width: 204px;
  height: 100%;
  background-color: var(--black-color);
  border-radius: 0 8px 8px 0;
  transition-duration: .5s;
  position: absolute;
  top: 0;
  transform: translate(-100%);
}

.nav-list-mobile {
  flex-direction: column;
  gap: 24px;
  margin-top: 88px;
  margin-left: 20px;
  display: flex;
}

.text-mobile {
  color: var(--white-color);
  font-weight: 400;
}

.navigation-activ {
  color: var(--orange-color);
}

.button-menu {
  text-align: center;
  color: var(--grey-color);
  cursor: pointer;
  z-index: 5;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 14.06px;
  display: none;
}

.backdrop {
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, .2);
  transition-duration: .5s;
  position: fixed;
  top: 0;
  transform: translate(-100%);
}

.hidden {
  transform: translate(0%);
}

.toggle-button {
  width: 60px;
  height: 24px;
  cursor: pointer;
  background: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  border: none;
  border-radius: 194px;
  align-items: center;
  gap: 24px;
  padding: 0 4px;
  display: flex;
}

.dark {
  display: none;
}

@media screen and (max-width: 767px) {
  .button-menu, .nav-mobile {
    display: flex;
  }

  .cinemania, .nav-list {
    display: none;
  }

  .mobile-menu {
    justify-content: space-between;
    align-items: center;
    display: flex;
  }

  .menu {
    height: 48px;
  }

  .logo {
    width: 46px;
  }

  .toggle-button {
    width: 44px;
    height: 18px;
    gap: 14px;
    padding: 0 3px;
  }
}

body[data-theme="dark"] .menu {
  background-color: var(--black-color);
}

body[data-theme="light"] .menu {
  background-color: var(--white-color);
}

